<template>
  <div>
        <el-card shadow>
          <div v-if="showResult">
            <div slot="header" style="display: flex; justify-content: center">
              <span></span>
            </div>
            <div>
              <el-form
                :model="form"
                :rules="rules"
                ref="OpinionRef"
                label-width="100px"
              >
                <el-form-item label="" prop="title">
                  <div class="input">
                    <el-input clearable v-model="form.title"></el-input>
                  </div>
                </el-form-item>
                <el-form-item label="" prop="content">
                  <el-input
                    type="textarea"
                    rows="10"
                    show-word-limit
                    maxlength="1000"
                    v-model="form.content"
                  ></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-switch v-model="anonymity"></el-switch>
                </el-form-item>
                <el-form-item label="">
                  <label for="file">
                    <div
                      style="
                        width: 100px;
                        height: 100px;
                        border: 1px dashed black;
                        line-height: 100px;
                        text-align: center;
                      "
                    >
                      <i class="el-icon-s-promotion" v-if="showImg"></i>
                      <img
                        :src="img"
                        style="width: 100px; height: 100px"
                        v-else
                        alt=""
                      />
                    </div>
                  </label>
                  <input
                    type="file"
                    id="file"
                    accept="image/*"
                    style="display: none"
                    @change="changeImg"
                  />
                </el-form-item>
                <el-form-item label="" prop="all">
                  <el-select name="" clearable id="" v-model="form.all">
                    <el-option value="" el-selected></el-option>
                    <el-option value=""></el-option>
                    <el-option value=""></el-option>
                    <el-option value=""></el-option>
                    <el-option value=""></el-option>
                    <el-option value=""></el-option>
                    <el-option value=""></el-option>
                    <el-option value=""></el-option>
                    <el-option value=""></el-option>
                    <el-option value=""></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="" prop="num">
                  <el-select name="" clearable id="" v-model="form.num">
                    <el-option value=""></el-option>
                    <el-option value=""></el-option>
                    <el-option value=""></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="" prop="phone">
                  <el-input
                    clearable
                    v-model="form.phone"
                    style="width: 50%"
                    type="number"
                  >
                    <el-select slot="prepend" v-model="form.selectPhone">
                      <el-option value=""></el-option>
                      <el-option value=""></el-option>
                      <el-option value=""></el-option>
                      <el-option value="QQ">QQ</el-option>
                    </el-select>
                  </el-input>
                </el-form-item>
              </el-form>
              <div style="display: flex; justify-content: flex-end">
                <el-button
                  style="background-color: #1e1e1e; color: white"
                  @click="submit('OpinionRef')"
                  ></el-button
                >
              </div>
            </div>
          </div>
          <el-result
            icon="success"
            v-else
            title=""
            :subTitle="subTitle"
          >
            <template slot="extra">
              <el-button @click="changeResult()" type="primary"></el-button>
            </template>
          </el-result>
        </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        phone: "",
        title: "",
        content: "",
        all: "",
        num: "",
        selectPhone: "",
      },
      rules: {
        phone: [
          { required: true, message: "" },
          { min: 8, max: 11, message: "" },
        ],
        title: [{ required: true, message: "" }],
        all: [{ required: true, message: "" }],
        content: [{ required: true, message: "" }],
        num: [{ required: true, message: "" }],
        selectPhone: [{ required: true, message: "" }],
      },
      showResult: true,
      anonymity: true,
      showImg: true,
      img: "",
      subTitle: "",
    };
  },
  methods: {
    //
    changeResult() {
      this.showResult = true;
    },
    //
    submit(formdata) {
      this.$refs[formdata].validate((e) => {
        if (e) {
          const loading = this.$loading({ text: "" });
          setTimeout(() => {
            loading.close();
            this.ToOpioion(formdata);
          }, 2000);
        } else {
          this.$message.warning({ message: "" });
        }
      });
    },
    //
    changeImg() {
      let file = document.getElementById("file").files[0];
      let reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = (e) => {
        this.img = e.target.result;
      };
      this.showImg = false;
    },
    //
    ToOpioion(formdata) {
      this.showResult = false;
      this.$refs[formdata].resetFields();
      setTimeout(() => {
        this.subTitle = "";
      }, 2000);
      setTimeout(() => {
        if (this.showResult == false) {
          this.showResult = true;
        }
      }, 5000);
    },
  },
};
</script>

<style lang="less" scoped>
.el-card {
  width: 70%;
  height: auto;
  margin: auto;
  .input {
    :deep(.el-input__inner) {
      border: 0px;
      border-bottom: 1px solid #1e1e1e;
      border-radius: 0px;
    }
  }
  :deep(.el-textarea__inner) {
    border: 1px solid #1e1e1e;
  }
}
</style>